import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/Appbar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import {fade} from '@material-ui/core/styles/colorManipulator';
import {withStyles} from "@material-ui/core/styles";
import SearchIcon from '@material-ui/icons/Search';
import { connectSearchBox } from 'react-instantsearch-dom';
import {Input} from "@material-ui/core";
import {makeStyles} from "@material-ui/styles";

const useStyles = makeStyles(theme => ({
    '@global': {
        '.algolia-autocomplete': {
            '& .ds-dropdown-menu': {
                boxShadow: theme.shadows[1],
                borderRadius: theme.shape.borderRadius,
                '&::before': {
                    display: 'none',
                },
                '& [class^=ds-dataset-]': {
                    border: 0,
                    maxHeight: 'calc(100vh - 100px)',
                    borderRadius: theme.shape.borderRadius,
                    backgroundColor: theme.palette.background.paper,
                },
            },
            '& .algolia-docsearch-suggestion--category-header-lvl0': {
                color: theme.palette.text.primary,
            },
            '& .algolia-docsearch-suggestion .algolia-docsearch-suggestion--subcategory-column': {
                opacity: 1,
                padding: '5.33px 10.66px',
                textAlign: 'right',
                width: '25%',
            },
            '& .algolia-docsearch-suggestion .algolia-docsearch-suggestion--content': {
                float: 'right',
                padding: '5.33px 0 5.33px 10.66px',
                width: '75%',
            },
            '& .algolia-docsearch-suggestion--subcategory-column-text': {
                color: theme.palette.text.secondary,
                fontWeight: theme.typography.fontWeightRegular,
            },
            '& .algolia-docsearch-suggestion--highlight': {
                color: theme.palette.type === 'light' ? '#174d8c' : '#acccf1',
            },
            '& .algolia-docsearch-suggestion': {
                textDecoration: 'none',
                backgroundColor: theme.palette.background.paper,
            },
            '& .algolia-docsearch-suggestion--title': theme.typography.h6,
            '& .algolia-docsearch-suggestion--text': theme.typography.body2,
            '&& .algolia-docsearch-suggestion--no-results': {
                width: '100%',
                '&::before': {
                    display: 'none',
                },
            },
            '& b': {
                fontWeight: theme.typography.fontWeightMedium,
            },
        },
    },
    root: {
        fontFamily: theme.typography.fontFamily,
        position: 'relative',
        marginRight: theme.spacing(2),
        marginLeft: theme.spacing(1),
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        '& $inputInput': {
            transition: theme.transitions.create('width'),
            width: 120,
            '&:focus': {
                width: 170,
            },
        },
    },
    search: {
        width: theme.spacing(9),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 9),
    },
}));

const styles = theme => ({
    root:{
        width: '100%',
    },
    grow:{
        flexGrow: 1,
    },
    menuButton:{
        marginLeft: -12,
        marginRight: 20,
    },
    title:{
        display: 'none',
        [theme.breakpoints.up('sm')]:{
            display: 'block',
        },
    },
    search:{
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover':{
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]:{
            marginLeft: theme.spacing.unit,
            width: 'auto',
        },
    },
    searchIcon:{
        width: theme.spacing.unit * 9,
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot:{
        color: 'inherit',
        width: '100%',
    },
    inputInput:{
        paddingTop: theme.spacing.unit,
        paddingRight: theme.spacing.unit,
        paddingBottom: theme.spacing.unit,
        paddingLeft: theme.spacing.unit * 10,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]:{
            width: 120,
            '&:focus':{
                width: 200,
            },
        },
    },
});

function SearchBox({currentRefinement, refine}, props){
    const {classes} = useStyles();
    const inputRef = React.useRef(null);
    return(
        <Input
            disableUnderline
            placeholder="Search?"
            inputProps={{
                'aria-label': 'Search',
            }}
            id="docsearch-input"
            inputRef={inputRef}
            classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
            }}
        />
    );
}
{/*<InputBase*/}
    {/*type='search'*/}
    {/*value={currentRefinement}*/}
    {/*onChange={event => refine(event.currentTarget.value)}*/}
    {/*placeholder="Search for Destination by Name, State, and keywords..."*/}
    {/*classes={{*/}
        {/*root: classes.inputRoot,*/}
        {/*input: classes.inputInput,*/}
    {/*}}*/}
{/*/>*/}

const CustomSearchBox = connectSearchBox(SearchBox);

function SearchAppBar(props){
    const {classes} = props;
    return(
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    <Typography className={classes.title} variant="h6" color='inherit' noWrap>
                        National Park Service Kiosk
                    </Typography>
                    <div className={classes.grow}/>
                    <div className={classes.search}>
                        <div className={classes.searchIcon}>
                            <SearchIcon/>
                        </div>
                        <CustomSearchBox/>
                    </div>
                </Toolbar>
            </AppBar>
        </div>
    );
}

SearchAppBar.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SearchAppBar);